import React, { useState, useEffect } from "react";
import "./nutricionistas.css";
import BarraBuscadora from "./BarraBuscadora";
import UltimosNutricionistas from "./Ultimos";
import TarjetaNutricionista from "./TarjetaNutricionista";

function Nutricionistas(props) {

  const [sugeridos, setSugeridos] = useState([]);
  const [buscados, setBuscados] = useState([]);
  const [ultimos, setUltimos] = useState([]);
  const [htmlBuscados, setHtmlBuscados] = useState();

  function buscarNutricionistas(filtros) {
    fetch('/nutricionistas/buscar', {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + props.token["token"],
        Accept: "application/json",
      },
      body: JSON.stringify(filtros),
    }).then((res) => res.json()).then(res => {
      console.log(res);
      setBuscados(res);
    })
  };

  function borrarBuscados() {
    setBuscados(sugeridos);
  }

  useEffect(() => {
    fetch(`/usuarios/${props.usuario["id"]}/nutricionistas`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + props.token["token"],
        Accept: "application/json",
      },
    }).then((res) => res.json()).then(res => {
      setUltimos(res);
    });
    fetch('/nutricionistas', {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + props.token["token"],
        Accept: "application/json",
      },
    }).then((res) => res.json()).then(res => {
      setSugeridos(res);
      setBuscados(res);
    });
  }, [props.token, props.usuario]);

  return (
    <div className="specialists">
      <aside>
        <img
          id="fotoBanner"
          src="https://www.perfectnursingservice.com/assets/img/banner-doctor.jpg"
          alt="FotoBanner"
        />
      </aside>
      <main className="container">
        <div id="pad2" className="row">
          <BarraBuscadora
            borrarBuscados={borrarBuscados}
            buscarNutricionistas={buscarNutricionistas}
            className="col-3 mt-4 mr-4"
          />
          <div className="col-5 mt-4">
            <h2>Resultado de búsqueda</h2>
            <div className="buscados mt-4">
            {buscados.map(nutricionista => (<TarjetaNutricionista nutricionista={nutricionista} key={nutricionista.id} />))}
            </div>
          </div >
          <div className="col-3 mt-4">
            {/*<UltimosNutricionistas className="col-12 col-lg-4" />*/}
            <h2>Últimos especialistas</h2>
            {ultimos.map(nutricionista => (<TarjetaNutricionista nutricionista={nutricionista} key={nutricionista.id} />))}
          </div>
        </div>
      </main>
    </div>
  );
}

export default Nutricionistas;
