import React, { useState, useEffect } from "react";
import "./barraBuscadora.css";

function BarraBuscadora(props) {

    const [busqueda, setBusqueda] = useState(false);
    const [botonADesplegar, setBotonADesplegar] = useState();
    const [filtroNombre, setFiltroNombre] = useState("");
    const [filtroExperiencia, setFiltroExperiencia] = useState(0);

    useEffect(() => {
        if (busqueda) {
            setBotonADesplegar(
                <button
                    type="button"
                    className="btn btn-outline-danger"
                    onClick={() => {
                        setBusqueda(false);
                        props.borrarBuscados();
                    }
                    }>
                    Borrar búsqueda
                </button>
            );
        } else {
            setBotonADesplegar(
                <button
                    type="button"
                    className="btn btn-outline-primary"
                    onClick={() => {
                        setBusqueda(true)
                        props.buscarNutricionistas({ nombre: filtroNombre, experiencia: filtroExperiencia });
                    }}>
                    Buscar
                </button>
            );
        }
    }, [busqueda, filtroExperiencia, filtroNombre, props]);

    return (
        <div className={"barra-buscadora " + props.className}>
            <h2>Barra Buscadora</h2>
            <div className="filtros">
                <div className="filtro filtro-nombre">
                    <label htmlFor="nombre">Nombre (opcional)</label>
                    <input
                        name="nombre"
                        type="search"
                        placeholder="Nombre"
                        aria-label="Filtro nombre"
                        onChange={e => setFiltroNombre(e.target.value)}
                    />
                </div>
                <div className="filtro filtro-experiencia">
                    <label htmlFor="experiencia">Experiencia (en años)</label>
                    <input
                        name="experiencia"
                        type="number"
                        min="0"
                        max="20"
                        placeholder="0"
                        aria-label="Filtro experiencia"
                        onChange={e => setFiltroExperiencia(e.target.value)}
                    />
                </div>
                <div className="filtro filtro-especializado">
                    <label htmlFor="especializado">Requerir especialización</label>
                    <input
                        name="especializado"
                        type="checkbox"
                        aria-label="Filtro especialización"
                    />
                </div>
                <div className="botones">
                    {botonADesplegar}
                </div>
            </div>
        </div>
    );
}

export default BarraBuscadora;