import "./navbar.css";
import logo from "../../assets/LogoNutrispecialist.png";


function Navbar({removeCookies}) {
    return (
        <div>
            <div id= "nav" className="row">
                <div id="contenedorHome" className="col-md-12">
                    <nav id="homepageTop" className="navbar navbar-expand">
                    <div className="navbar-collapse">
                        <ul className="navbar-nav me-auto">
                            <li className="nav-item">
                                <a className="nav-link" href="/"> 
                                <img
                                    id="logo"
                                    src={logo}
                                    alt="Logo"
                                /></a>
                            </li>
                        </ul>
                    </div>
                    <div className="navbar-collapse">
                        <ul className="navbar-nav m-auto">
                        <li className="nav-item">
                            <a id="opcionBalance" className="nav-link active" href="/balance">
                                Balance nutricional
                            </a>
                        </li>
                        </ul>
                    </div>
                    <div className="navbar-collapse">
                        <ul className="navbar-nav m-auto">
                        <li className="nav-item">
                            <a
                            id="opcionEspecialista"
                            className="nav-link"
                            href="/nutricionistas"
                            >Nutricionistas</a
                            >
                        </li>
                        </ul>
                    </div>
                    <div className="navbar-collapse">
                        <ul className="navbar-nav m-auto">
                        <li className="nav-item">
                            <a id="opcionForo" className="nav-link" href="/foro">
                                Foro
                            </a>
                        </li>
                        </ul>
                    </div>
                    <div id="botonSalir" className="navbar-collapse">
                        <ul className="navbar-nav ms-auto">
                        <li className="nav-item">
                            <a id="opcionSalir" className="nav-link" href="/" onClick={removeCookies}>
                                Salir
                            </a>
                        </li>
                        </ul>
                    </div>
                    </nav>
                </div>
            </div>
        </div>
    );
}

export default Navbar;