import { Line } from "react-chartjs-2";
import React from "react";

let xValues = ["Jan","Feb","Mar","Jun","Jul","Aug","Sep"];
let yValues = [];

const data = (datos) => {
  datos.datos.forEach(e => {
    if(e.UsuarioId === datos.usuario.id)
      yValues.push(e.bmi);
  });

  return {
      labels: xValues,
      datasets: [{
          label: 'Progreso',
          fill: false,
          lineTension: 0.5,
          radius: 7,
          backgroundColor: "rgba(255,255,255)",
          borderColor: "rgba(146,163,253)",
          data: yValues
      }]
  }
};

function myChart(datos){
return(
    <>
    <Line data={data(datos)}/>
    </>
)};

export default myChart;