
function TarjetaNutricionista(props) {
    return (
        <div className="card col-6">
            <h5 className="card-title">{props.nutricionista.nombre}</h5>
            <img
                className="card-img-top"
                src={props.nutricionista.foto}
                alt="Card"
            />
            <div className="card-body">
                <p>{props.nutricionista.correo}</p>
                <p>Nota: {props.nutricionista.calificacion}</p>
            </div>
        </div>
    );
}

export default TarjetaNutricionista;