import "./balanceN.css";
import React, {useState, useEffect} from "react";
import imagenMenuPrincipal from "../../assets/imagenMenuPrincipal.png";
import comida1 from "../../assets/comida1.png";
import comida2 from "../../assets/comida2.png";
import plato1 from "../../assets/plato1.png";
import plato2 from "../../assets/plato2.png";
import reminderOff from "../../assets/reminderOff.jpg";
import reminderOn from "../../assets/reminderOn.jpg";
import Chart from "./Chart.js";

function Balance(props){
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch("http://localhost:3001/progreso",{
      method: "GET",
      headers: {
          "Content-Type":"application/json",
          Authorization: "Bearer " + props.token["token"],
          Accept: "application/json",
      },
    }).then(res => res.json()).then(res => {
      setData(res);
    });
  }, []);

  return (
    <div className="container-fluid">
      <div className="row">
        <h2 id="tituloMenuPrincipal">Menú principal</h2>
        <img
          id="imagenMenuPrincipal"
          src={imagenMenuPrincipal}
          alt="menuPrincipal"
        />
      </div>
      <br/>
      <div className="row">
        <div className="col-md-9">
          <div className="card border-light">
            <div className="card-body">
              <h2 className="card-title fw-bold">Balance Nutricional</h2>
              <div className="dropdown" id="seleccionarTiempo">
                <a
                  className="btn btn-secondary dropdown-toggle"
                  href="/"
                  role="button"
                  id="dropdownMenuLink"
                  data-bs-toggle="dropdown"
                  aria-expanded="false">
                  Tiempo
                </a>
                <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
                  <li><a className="dropdown-item" href="/">Diario</a></li>
                  <li><a className="dropdown-item" href="/">Semanal</a></li>
                  <li><a className="dropdown-item" href="/">Mensual</a></li>
                </ul>
              </div>
            </div>
            <div id="ChartProgreso">
              <Chart datos={data} usuario={props.usuario}/>
            </div>
          </div>
        </div>
        <div className="col-md-3">
          <div className="card border-light">
            <div className="card-body" id="tuDieta">
              <h5 className="card-title">Tu dieta</h5>
              <a href="/" className="btn btn-secondary" id="tuDietaEdit">Editar</a>
            </div>
          </div>
          <br />
          <div className="card border-light">
            <div className="card-body">
              <h5 className="card-title fw-bold">Hoy</h5>
              <div className="dropdown" id="seleccionarComida">
                <a
                  className="btn btn-secondary dropdown-toggle"
                  href="/"
                  role="button"
                  id="dropdownMenuLink"
                  data-bs-toggle="dropdown"
                  aria-expanded="false">
                  Comida
                </a>
                <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
                  <li><a className="dropdown-item" href="/">Desayuno</a></li>
                  <li><a className="dropdown-item" href="/">Almuerzo</a></li>
                  <li><a className="dropdown-item" href="/">Cena</a></li>
                </ul>
              </div>
              <br />
              <div className="card shadow p-3 mb-5 bg-body" id="comida1">
                <div className="row g-0">
                  <div className="col-md-3">
                    <img
                      src={comida1}
                      className="img-fluid rounded-start"
                      alt="comida 1"
                      id="imagenComida1"
                    />
                  </div>
                  <div className="col-md-7">
                    <div className="card-body">
                      <h5 className="card-title">Sushi</h5>
                      <p className="card-text">
                        <small className="text-muted">Hoy | 7am</small>
                      </p>
                    </div>
                  </div>
                  <div className="col-md-2">
                    <img
                      src={reminderOff}
                      className="img-fluid rounded-circle"
                      alt="recordatorio apagado"
                      id="recordatorio1"
                    />
                  </div>
                </div>
              </div>
              <div className="card shadow p-3 mb-5 bg-body" id="comida2">
                <div className="row g-0">
                  <div className="col-md-3">
                    <img
                      src={comida2}
                      className="img-fluid rounded-start"
                      alt="comida 2"
                      id="imagenComida2"
                    />
                  </div>
                  <div className="col-md-7">
                    <div className="card-body">
                      <h5 className="card-title">Té verde</h5>
                      <p className="card-text">
                        <small className="text-muted">Hoy | 8am</small>
                      </p>
                    </div>
                  </div>
                  <div className="col-md-2">
                    <img
                      src={reminderOn}
                      className="img-fluid rounded-circle"
                      alt="recordatorio apagado"
                      id="recordatorio2"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <br />
          <div className="card border-light">
            <div className="card-body">
              <h5 className="card-title fw-bold">Platos</h5>
            </div>
            <div className="row row-cols-1 row-cols-md-2 g-4">
              <div className="col">
                <div className="card" id="plato1">
                  <img
                    src={plato1}
                    className="card-img-top mx-auto"
                    alt="plato 1"
                    id="imagenPlato1"                    
                  /><br/>
                  <div className="card-body">
                    <h5 className="card-title">Pie de manzana</h5>
                    <br />
                    <a href="/" className="btn btn-secondary" id="seleccionarPlato1">Seleccionar</a>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="card" id="plato2">
                  <img
                    src={plato2}
                    className="card-img-top mx-auto"
                    alt="plato 2"
                    id="imagenPlato2"
                  /><br/>
                  <div className="card-body">
                    <h5 className="card-title">Panecook</h5>
                    <br/>
                    <a href="/" className="btn btn-secondary" id="seleccionarPlato2">Seleccionar</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    );
}

export default Balance;